<template>
    <div class="new-address">
        <van-nav-bar title="修改地址" left-text="返回" left-arrow @click-left="$router.go(-1)" />

        <van-address-edit
            :area-list="areaList"
            show-postal
            show-delete
            show-set-default
            :address-info="addressInfo"
            :search-result="searchResult"
            :area-columns-placeholder="['请选择', '请选择', '请选择']"
            @save="onEdit"
            @delete="onDelete"
        />
    </div>
</template>

<script>
    import { Toast } from "vant";
    // 引入地址数据信息
    import areaList from "../assets/js/address";

    export default {
        data() {
            return {
                areaList,
                searchResult: [],
                // 地址id
                aid: null,
                //地址信息
                addressInfo: {},
            };
        },

        methods: {
            // 修改地址
            onEdit({ name, tel, province, city, county, postalCode, areaCode,addressDetail, isDefault }) {
                // 获取token
                let tokenString = localStorage.getItem("Tk");

                let data = {
                    appkey: this.appkey,
                    tokenString,
                    aid: this.aid,
                    name, // 姓名
                    tel, // 电话
                    province, // 省份
                    city, // 城市
                    county, // 区县
                    postalCode, // 邮政编码
                    areaCode, // 地区编号
                    addressDetail, // 详细地址
                    isDefault: Number(isDefault), // 默认地址
                }

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "POST",
                    url: "/editAddress",
                    data,
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    if(res.data.code == 30000) {
                        
                        this.$router.push({ name: "Address" });

                    } else{
                        Toast(res.data.msg);
                    }

                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();


                });
            }, 
 
            // 根据id获取地址数据
            getAddressData(aid) {
                // 获取token
                let tokenString = localStorage.getItem("Tk");

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "GET",
                    url: "/findAddressByAid",
                    params: {
                        appkey: this.appkey,
                        tokenString,
                        aid,
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    if(res.data.code == 40000) {

                        res.data.result.find(item => {
                            item.isDefault = Boolean(item.isDefault);
                        });

                        this.addressInfo = res.data.result[0];

                    }

                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();


                });
            },

            // 删除地址
            onDelete() {
                // 获取token
                let tokenString = localStorage.getItem("Tk");

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "POST",
                    url: "/deleteAddress",
                    data: {
                        appkey: this.appkey,
                        tokenString,
                        aid: this.aid,
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    if (res.data.code == 10000) {
                        Toast.success(res.data.msg);
                        this.$router.push({ name: "Address" });
                    } else {
                        Toast(res.data.msg);
                    }

                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();


                });
            },

        },

        created() {
            this.aid = this.$route.query.id;

            this.getAddressData(this.aid);
        },
    }
</script>

<style lang="less" scoped>
    
</style>